import React, { useEffect, useRef } from 'react'
import * as PIXI from 'pixi.js'
import { Subject, BehaviorSubject, fromEvent } from 'rxjs'
import { map, takeUntil } from 'rxjs/operators'

interface SlotRendererProps {
    grid: string[][]
    winningRows: number[]
    spinning: boolean
}

const SlotRenderer: React.FC<SlotRendererProps> = ({
    grid,
    winningRows,
    spinning,
}) => {
    const containerRef = useRef<HTMLDivElement>(null)
    const pixiAppRef = useRef<PIXI.Application | null>(null)
    const destroySubject$ = new Subject<void>()

    useEffect(() => {
        // Create PIXI Application
        const app = new PIXI.Application({
            width: 800,
            height: 300,
            backgroundColor: 0x1099bb,
            resizeTo: containerRef.current || undefined,
        })

        pixiAppRef.current = app

        // Mount PIXI App
        if (containerRef.current) {
            containerRef.current.appendChild(app.canvas)
        }

        // Create reels
        const reelTextures = createReelTextures(app)
        const reelContainers = createReelContainers(app, reelTextures, grid)

        // Animate winning rows
        if (winningRows.length > 0) {
            animateWinningRows(reelContainers, winningRows)
        }

        // Spinning animation
        if (spinning) {
            animateSpinning(reelContainers)
        }

        // Cleanup
        return () => {
            destroySubject$.next()
            destroySubject$.complete()
            app.destroy(true)
        }
    }, [grid, winningRows, spinning])

    // Function to create reel textures
    const createReelTextures = (app: PIXI.Application) => {
        const spriteSheet = PIXI.Texture.from('/images/sprites-reels.webp')
        const symbols = ['j', 'a', 'd', 'k', 'w', 'q']

        return symbols.map((symbol) => {
            const { x, y } = getSymbolPosition(symbol)
            const texture = new PIXI.Texture(
                spriteSheet.baseTexture,
                new PIXI.Rectangle(x, y, 160, 160)
            )
            texture.label = symbol // Add label to identify symbol
            return texture
        })
    }

    // Function to get symbol position
    const getSymbolPosition = (symbol: string) => {
        const spriteMap: Record<string, { x: number; y: number }> = {
            j: { x: 0, y: -8 },
            a: { x: -160, y: -8 },
            d: { x: -320, y: -8 },
            k: { x: 0, y: -165 },
            w: { x: -160, y: -165 },
            q: { x: -320, y: -165 },
        }
        return spriteMap[symbol] || { x: 0, y: -100 }
    }

    // Create reel containers
    const createReelContainers = (
        app: PIXI.Application,
        textures: PIXI.Texture[],
        grid: string[][]
    ) => {
        return grid.map((row, rowIndex) => {
            const reelContainer = new PIXI.Container()

            row.forEach((symbol, colIndex) => {
                const sprite = new PIXI.Sprite(
                    textures.find((t) => t.label === symbol) || textures[0]
                )
                sprite.y = colIndex * 160
                reelContainer.addChild(sprite)
            })

            reelContainer.x = rowIndex * 160
            app.stage.addChild(reelContainer)
            return reelContainer
        })
    }

    // Animate winning rows
    const animateWinningRows = (
        reelContainers: PIXI.Container[],
        winningRows: number[]
    ) => {
        winningRows.forEach((row) => {
            const reelContainer = reelContainers[row]
            // Add winning animation logic
        })
    }

    // Spinning animation
    const animateSpinning = (reelContainers: PIXI.Container[]) => {
        reelContainers.forEach((reel) => {
            // Add spinning animation logic
        })
    }

    return <div ref={containerRef} style={{ width: '100%', height: '300px' }} />
}

export default SlotRenderer
