'use client'
import React, { useState, useEffect } from 'react'
import { Card, CardContent } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { useRxSocket } from '../../custom-hooks/useRxSocket'
import PixiSlotRenderer from '@/app/components/PixiSlotRenderer'
import WinOverlay from '@/app/components/WinOverlay'
import { takeUntil } from 'rxjs/operators'
import { Subject } from 'rxjs'

const SlotGame = () => {
    const userId = 'user_123'
    const { gameState$, spinResult$, error$, spin } = useRxSocket(userId)
    const [grid, setGrid] = useState<string[][]>([
        ['', '', '', '', ''],
        ['', '', '', '', ''],
        ['', '', '', '', ''],
    ])
    const [spinning, setSpinning] = useState<boolean>(false)
    const [balance, setBalance] = useState<number>(1000)
    const [betAmount, setBetAmount] = useState<number>(10)
    const [lastWinnings, setLastWinnings] = useState<number>(0)
    const [winning, setWinning] = useState<number>(0)
    const [winningRows, setWinningRows] = useState<number[]>([])
    const [showWinOverlay, setShowWinOverlay] = useState<boolean>(false)
    const [spinCount, setSpinCount] = useState(0)
    const destroy$ = new Subject<void>()

    // RxJS-based side effects
    useEffect(() => {
        const destroy$ = new Subject<void>()

        // Game State Subscription
        const gameStateSub = gameState$
            .pipe(takeUntil(destroy$))
            .subscribe((state) => {
                if (state) {
                    // Handle game state if needed
                }
            })

        // Spin Result Subscription
        const spinResultSub = spinResult$
            .pipe(takeUntil(destroy$))
            .subscribe((result) => {
                setGrid(result.grid)
                setWinning(result.winnings)
                setWinningRows(result.winningRows)
                setBalance(result.balance)
                setLastWinnings(result.winnings)

                if (result.winnings > 0) {
                    setShowWinOverlay(true)
                }
            })

        // Error Handling
        const errorSub = error$
            .pipe(takeUntil(destroy$))
            .subscribe((errorMsg) => {
                console.error(errorMsg)
                // Implement error handling
            })

        return () => {
            destroy$.next()
            destroy$.complete()
            gameStateSub.unsubscribe()
            spinResultSub.unsubscribe()
            errorSub.unsubscribe()
        }
    }, [gameState$, spinResult$, error$])

    const handleSpin = () => {
        setSpinCount((prevCount) => prevCount + 1)
        setSpinning(true)

        // Simplified spin call
        spin(betAmount, balance, spinCount)

        setTimeout(() => {
            setSpinning(false)
        }, 2000)
    }

    return (
        <>
            <div className="min-h-screen bg-gradient-to-br from-[#1a1a2e] via-[#16213e] to-[#0f3460] flex items-center justify-center p-4">
                <Card className="w-full max-w-4xl bg-[#0f3460]">
                    {/* PixiJS Renderer */}
                    <PixiSlotRenderer
                        grid={grid}
                        winningRows={winningRows}
                        spinning={spinning}
                    />

                    <CardContent className="p-4">
                        {/* Rest of your existing UI components */}
                        <div className="flex flex-col sm:flex-row justify-between items-center space-y-4">
                            <Button onClick={handleSpin} disabled={spinning}>
                                SPIN
                            </Button>
                        </div>
                    </CardContent>
                </Card>
            </div>
        </>
    )
}

export default SlotGame
